// =============================================================================
// HERO UNIT WIDGET STYLES
// =============================================================================
.doryo-hero {
  position: relative;
  display: flex;
  padding: 4rem 1rem 0 0;
  height: 55vh;
  max-width: 1200px;
  margin: 0 auto;

  @include respond-to("mobile") {
    height: 60vh;
    min-height: 500px;
  }

  @include respond-to("tablet") {
    padding: 4rem 4rem 0 0;
    height: 80vh;
  }

  @include respond-to("desktop") {
    padding: 8rem 4rem 0 0;
  }

  &__flag {
    border-radius: 6px;
    border: 1px solid #abefc6;
    background: #ecfdf3;
    padding: 1px 8px 3px;
    display: inline-flex;
    color: var(--Component-colors-Utility-Success-utility-success-700, #067647);
    text-align: center;
    position: relative;
    align-items: center;
    gap: 6px;

    font-size: var(--font-size-xs, 12px);
    font-weight: var(--font-weight-medium);
    line-height: var(--line-height-normal, 20px);

    @include respond-to("tablet") {
      font-size: var(--font-size-sm, 14px);
    }

    &::before {
      content: "";
      width: 8px;
      height: 8px;
      background-color: #17b26a;
      border-radius: 4px;
      margin-top: 2px;
      animation: blink 0.8s infinite alternate;
    }
  }

  &__blob {
    position: absolute;
    left: -5vh;
    bottom: 2vh;

    width: 100%;
    height: auto;
    aspect-ratio: 1; // Quadratisch halten
    max-width: 80%;
    max-height: 80%;
    display: block;

    // Responsive breakpoints
    @include respond-to("tablet") {
      max-width: 60%;
      max-height: 60%;
      left: -6vh;
      bottom: 4vh;
    }
  }

  &__avatar {
    display: flex;
    align-items: flex-end;
  }

  &__image {
    height: 100vh;
    margin-left: -4rem;
    position: relative;
    pointer-events: none;

    @include respond-to("mobile") {
      margin-left: unset;
    }

    @include respond-to("tablet") {
      height: 80vh;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: bottom;
    }
  }

  &__content {
    margin-left: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    position: relative;

    @include respond-to("tablet") {
      margin-left: 4rem;
    }

    @include respond-to("desktop") {
      margin-left: 6rem;
    }
  }

  &__title {
    font-size: var(--font-size-3xl);

    @include respond-to("tablet") {
      font-size: var(--font-size-4xl);
    }

    @include respond-to("desktop") {
      font-size: var(--font-size-5xl);
    }
  }

  &__subtitle {
    color: var(--e-global-color-text);
    font-size: var(--font-size-base);
    line-height: var(--line-height-normal);
    display: block;
    font-weight: var(--font-weight-normal);
    padding-top: 1rem;

    @include respond-to("tablet") {
      font-size: var(--font-size-lg);
      padding-top: 2rem;
    }

    @include respond-to("desktop") {
      font-size: var(--font-size-xl);
    }
  }

  &__button-wrapper {
    margin-top: 2rem;
    @include respond-to("tablet") {
      margin-top: 2.6rem;
    }
  }
}

footer {
  margin-top: 8rem;
}

@keyframes blink {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0.5;
    transform: scale(0.8);
  }
}
