.doryo-newsletter {
  display: flex;
  padding: var(--spacing-3xl);
  align-items: flex-start;
  align-content: flex-start;
  gap: 32px var(--spacing-xl);
  align-self: stretch;
  flex-wrap: wrap;
  border-radius: var(--radius-2xl, 16px);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: var(--shadow-lg);
  color: #ffffff;

  @include respond-to('tablet') {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: var(--spacing-2xl, 32px);
    padding: var(--spacing-4xl);
  }

  @include respond-to('desktop') {
    padding: var(--spacing-5xl, 64px);
  }

  &__content {
    flex: 1;
    max-width: 38rem;
  }

  &__title {
    margin: 0 0 var(--spacing-base) 0;
    font-size: var(--font-size-3xl);
    font-weight: var(--font-weight-semibold);
    line-height: 1.1;

    @include respond-to('tablet') {
      font-size: var(--font-size-4xl);
    }
  }

  &__description {
    margin: 0;
    font-size: var(--font-size-lg);
    line-height: 1.6;
    color: rgba(255, 255, 255, 0.85);

    p {
      margin: 0 0 var(--spacing-sm) 0;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__form {
    flex: 0 0 100%;
    display: flex;
    flex-direction: column;
    gap: var(--spacing-base);

    @include respond-to('tablet') {
      flex: 0 0 auto;
      min-width: 22rem;
    }
  }

  &__fields {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-sm);

    @include respond-to('tablet') {
      flex-direction: row;
      align-items: center;
      gap: var(--spacing-sm);
    }
  }

  &__input {
    width: 100%;
    border: none;
    border-radius: var(--radius-2xl);
    padding: var(--spacing-md) var(--spacing-xl);
    font-size: var(--font-size-base);
    color: var(--color-gray-900, #1f2933);
    background-color: #ffffff;
    box-shadow: 0 10px 30px rgba(10, 68, 108, 0.15);
    transition: var(--transition-base);

    &:focus {
      outline: none;
      box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.35), 0 14px 30px rgba(10, 68, 108, 0.15);
    }

    &::placeholder {
      color: rgba(31, 41, 51, 0.6);
    }
  }

  &__button {
    @include button-style(#2d58ff, #ffffff);
    border-radius: var(--radius-2xl);
    white-space: nowrap;
    padding: var(--spacing-md) var(--spacing-2xl);
    box-shadow: 0 12px 24px rgba(45, 88, 255, 0.35);

    &:hover {
      box-shadow: 0 16px 32px rgba(45, 88, 255, 0.35);
    }
  }

  &__privacy {
    margin: 0;
    font-size: var(--font-size-sm);
    color: rgba(255, 255, 255, 0.75);

    a {
      color: inherit;
      text-decoration: underline;
      transition: var(--transition-fast);

      &:hover,
      &:focus {
        color: #ffffff;
      }
    }
  }

  .screen-reader-text {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
  }

  @media (max-width: 480px) {
    padding: var(--spacing-2xl);

    &__title {
      font-size: var(--font-size-2xl);
    }

    &__description {
      font-size: var(--font-size-base);
    }

    &__button {
      width: 100%;
      justify-content: center;
    }
  }
}
