.doryo-services {
  display: grid;
  grid-template-columns: 1fr;
  gap: var(--spacing-3xl);
  align-items: start;
  margin-top: var( --spacing-5xl);
  margin-bottom: var(--spacing-6xl);

  @include respond-to("tablet") {
    grid-template-columns: 1fr 1fr;
    gap: var(--spacing-5xl);
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.5rem;

    @include respond-to("mobile") {
      grid-template-columns: repeat(2, 1fr);
      gap: var(--spacing-2xl) var(--spacing-4xl);
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-base);
  }

  &__icon {
    flex-shrink: 0;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--e-global-color-46cb5a6); 

    border-radius: var(--radius-lg, 10px);
    border: 1px solid var(--e-global-color-b07e2f9);

    box-shadow: 0 0 0 1px rgba(10, 13, 18, 0.18) inset, 0 -2px 0 0 rgba(10, 13, 18, 0.05) inset, 0 1px 2px 0 rgba(10, 13, 18, 0.05);

    i {
      font-size: 1.25rem;
    }

    svg {
      width: 1.25rem;
      height: 1.25rem;
    }
  }

  &__content {
    flex: 1;
  }

  &__title {
    margin: 0 0 0.5rem 0;
    font-weight: var(--font-weight-semibold);
    font-size: var(--font-size-lg);
    line-height: var(--line-height-relaxed);
    color: var(--e-global-color-primary);
  }

  &__description {
    margin: 0;
    color: var(--e-global-color-text);
    line-height: var(--line-height-relaxed);
  }

  &__image {
    width: 100%;
    
    overflow: hidden;
    border-radius: 8px;
    position: relative;
    align-self: flex-end;
    margin-bottom: -1.25rem;

    @include respond-to("tablet") {
      height: 600px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: left center; 
    }
  }
}
