.doryo-contact {
  display: flex;
  flex-direction: column;
  gap: 2rem;

  &__item {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
  }

  &__icon {
    flex-shrink: 0;
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: var(--e-global-color-0b7432d);
    color: var(--e-global-color-ff7c44b);

    i {
      font-size: 1.25rem;
    }

    svg {
      width: 1.25rem;
      height: 1.25rem;
    }
  }

  &__content {
    flex: 1;
    min-width: 0; // Prevent overflow
  }

  &__title {
    margin: var(--spacing-sm) 0 0.5rem 0;
    font-weight: var(--font-weight-semibold);
    font-size: 1.125rem;
    color: var(--e-global-color-primary);
  }

  &__text {
    margin: 0 0 0.75rem 0;
    color: var(--e-global-color-text);
    line-height: 1.6;
    font-size: 0.9rem;

    p {
      margin: 0 0 0.5rem 0;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__link {
    display: inline-block;
    color: var(--e-global-color-accent);
    text-decoration: none;
    font-weight: var(--font-weight-medium);
    font-size: 1rem;
    transition: all 0.3s ease;

    &:hover {
      color: var(--e-global-color-accent);
      text-decoration: underline;
      transform: translateX(2px);
    }

    &:focus {
      outline: 2px solid var(--e-global-color-accent);
      outline-offset: 2px;
      border-radius: 4px;
    }
  }

  // Mobile optimizations
  @media (max-width: 480px) {
    gap: 1.5rem;

    &__item {
      gap: 0.75rem;
    }

    &__icon {
      width: 2.5rem;
      height: 2.5rem;

      i {
        font-size: 1rem;
      }

      svg {
        width: 1rem;
        height: 1rem;
      }
    }

    &__title {
      font-size: 1rem;
    }

    &__text {
      font-size: 0.875rem;
    }

    &__link {
      font-size: 0.9rem;
      word-break: break-all; // Allow breaking long email addresses
    }
  }
}
