.doryo-faq {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    border-top: 1px solid rgba(31, 41, 51, 0.12);
    padding: var(--spacing-xl) 0;

    &:last-child {
      border-bottom: 1px solid rgba(31, 41, 51, 0.12);
    }
  }

  &__details {
    margin: 0;
  }

  &__summary {
    display: flex;
    align-items: flex-start;
    gap: var(--spacing-xl);
    cursor: pointer;
    outline: none;
    list-style: none;

    &::-webkit-details-marker {
      display: none;
    }
  }

  &__question {
    font-size: var(--font-size-lg);
    line-height: var(--line-height-relaxed);
    color: var(--e-global-color-secondary, #1f2937);
    font-weight: var(--font-weight-medium);
  }

  details[open] &__question {
    font-weight: var(--font-weight-semibold);
  }

  &__icons {
    margin-left: auto;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  &__icon {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 2px solid rgba(145, 158, 171, 0.35);
    display: grid;
    place-items: center;
    color: rgba(145, 158, 171, 1);
    transition: var(--transition-fast);
  }

  &__icon--close {
    display: none;
  }

  details[open] &__icon--open {
    display: none;
  }

  details[open] &__icon--close {
    display: grid;
  }

  &__content {
    margin-top: var(--spacing-lg);
    color: var(--e-global-color-secondary, #1f2937);
    font-size: var(--font-size-base);
    line-height: var(--line-height-relaxed);

    p {
      margin: 0 0 var(--spacing-base) 0;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

